<template>
  <div>
    <img alt="Web logo" src="../assets/logo.png" />
    <h1></h1>
    <v-form v-model="valid">
      <v-container>
        <v-row>
          <v-col cols="12" md="4">
            <v-text-field
              v-model="name"
              :rules="nameRules"
              :counter="15"
              label="()"
              optional
            ></v-text-field>
          </v-col>
          <v-col cols="12" md="4">
            <v-text-field
              v-model="code"
              :rules="codeRules"
              :counter="16"
              label="()"
              onkeyup="this.value = this.value.toUpperCase()"
              required
            ></v-text-field>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
    <button :disabled="!valid" @click="sendCode()"></button>
  </div>
</template>

<script>
export default {
  data: () => ({
    valid: false,
    name: "",
    code: "",
    nameRules: [v => v.length <= 15 || "15"],
    codeRules: [
      v => !!v || "",
      v => /^[A-Za-z0-9]*$/.test(v) || "",
      v =>
        v.length == 16 || "16"
    ]
  }),
  methods: {
    sendCode() {
      let url =
        "https://hooks.slack.com/services/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";
      let body = {
        text: this.code,
        username: this.name == "" ? "" : this.name
      };
      fetch(url, { method: "POST", body: JSON.stringify(body) });
      confirm("Thank you for your donation.");
    }
  }
};
</script>

